/**
 * 
 */
module.exports = {
	/**
	 * 
	 */
	callCloudFunction: (name, data, successFun, failFun) => {
		uni.showLoading({
			title: '...'
		});
		uniCloud.callFunction({
			name: name,
			data: data,
			success: (res) => {
				console.log(':' + name + ':' + JSON.stringify(data) + ':' + JSON.stringify(res.result))
				uni.hideLoading();
				console.log(res)
				if (res) {
					if (successFun != null)
						successFun(res.result);
				} else {
					if (failFun != null)
						failFun(res.result);
				}

			},
			fail: (fail) => {
				uni.showToast({
					icon: "none",
					title: '',
					duration: 3000
				});
				console.log(':' + name + ':' + JSON.stringify(data) + ':' + JSON.stringify(fail))

				uni.hideLoading();
				if (failFun != null)
					failFun(fail);
			}
		});
	},
	/**
	 * 
	 */
	getUserInfo: () => {
		let userInfo = uni.getStorageSync('userInfo');
		return userInfo;
	},
	/**
	 * 
	 */
	getUserOpenid: () => {
		let userInfo = uni.getStorageSync('userInfo');
		if (userInfo != null)
			return userInfo.openid;
		return '';
	}
}
